import { AcUnit, Cancel, Edit, HourglassBottom, PendingActions, Save, Shower, Straighten, Thermostat, Water, WbSunny } from "@mui/icons-material";
import { styled } from "@mui/system";
import React, { useState } from "react";
import useSettings from "../queries/useSettings";
import { IconButton, Input, InputAdornment, TextField } from "@mui/material";
import useChangeSettings from "../mutations/useChangeSettings";

const iconMap = {
  'MIN_HUMIDITY': Water,
  'MIN_WATER_LEVEL': Straighten,
  'TIME_BETWEEN_MISTING': PendingActions,
  'TIME_TO_MIST': HourglassBottom
}

const settingsMap = {
  'MIN_HUMIDITY': {
    icon: Water,
    name: "Minimum humidity",
    unit: "%",
    format: (number) => `${number}%`
  },
  'MIN_WATER_LEVEL': {
    icon: Straighten,
    name: "Minimum water level",
    unit: "cm",
    format: (number) => `${number}% full (${number}cm)`
  },
  // 'TIME_BETWEEN_MISTING': {
  //   icon: PendingActions,
  //   name: "Minimum humidity",
  //   format: (number) => `${number} seconds`
  // },
  'TIME_TO_MIST': {
    icon: HourglassBottom,
    name: "How long to mist",
    unit: "seconds",
    format: (number) => `${number} seconds`
  }
}

const Setting = styled('div', {})({
  fontWeight: 100,
  fontSize: 22,
  padding: '10px 25px',
  margin: 10,
  '@media (max-width: 450px)': {
    fontSize: '5vw',
  },
  background: 'rgba(0,0,0,.1)',
  borderRadius: 25,
  display: 'flex',
  // alignItems: 'center',
  // justifyContent: 'center',
  flexDirection: 'column'
})


const Wrapper = styled('div', {})({
  width: '100%',
  maxWidth: 350,
  padding: '0px 10px',
  maxHeight: '100%',
  flexShrink: 1,
  overflowY: 'auto',
})

const FlexBox = styled('div', {})({
  display: 'flex',
  alignItems: 'center',
})

const ActionWrapper = styled('div', {})({
  display: 'flex',
  alignItems: 'center',
})

const Unit = styled('div', {})({
  fontSize: 18,
  margin: '0 5px 0 10px',
  background: 'rgba(0,0,0,0.1)',
  padding: '0px 5px',
  borderRadius: 10,
  userSelect: 'none'
})



function EditableField({
  value: _value,
  unit,
  onChange
}) {
  const [editing, setEditing] = useState(false);
  const [value, setValue] = useState(_value);

  const cancel = () => {
    setValue(_value);
    setEditing(false);
  }

  return (<FlexBox>
      <FlexBox>
        <TextField 
          value={value}
          onChange={e => setValue(e.target.value)}
          hiddenLabel 
          variant='filled' 
          size="small" 
          margin="dense"
          disabled={!editing}
          type="number"
          InputProps={{
            sx: { fontFamily: 'inherit', fontSize: 'inherit' },
            endAdornment: <InputAdornment>
              <FlexBox>
                <Unit>{unit}</Unit>
                <ActionWrapper>
                  {editing 
                    ? <>
                        <IconButton onClick={cancel} size="small"><Cancel/></IconButton>
                        <IconButton onClick={() => onChange(value, { onSuccess: () => setEditing(false) })} size="small"><Save/></IconButton>
                      </> 
                    : <IconButton onClick={() => setEditing(true)} size="small"><Edit/></IconButton>
                  }
                </ActionWrapper>
              </FlexBox>
            </InputAdornment>
          }} 
          
        />
      </FlexBox>
      
    </FlexBox>
  );
}

function Settings() {
  const { data: settings } = useSettings();
  const { mutate: save } = useChangeSettings();
  
  // const {}

  const onSave = (key, value, meta) => {
    save({ key, value: Number(value) }, meta)
  }

  if (!settings) return null;
  return <Wrapper>
    {Object.entries(settings).map(([key, value]) => {
      const setting = settingsMap[key];
      if (!setting) return null;
      return <Setting>
        <FlexBox>
          {React.createElement(setting.icon, { sx: { fontSize: '30px', marginRight: 1} })} {setting.name}
        </FlexBox>
        <EditableField value={value} unit={setting.unit} onChange={(val, meta) => onSave(key, val, meta)}/>
      </Setting>;
    })}
  </Wrapper>
}

export default Settings;